import React, { Fragment } from 'react'
// import PropTypes from 'prop-types'
import { connect } from 'dva'
// import { routerRedux } from 'dva/router'
import { Row, Col, Input, Table, Button, Modal } from 'antd'
// import queryString from 'query-string'
import Model from './Model'
import { config } from '../signupConfig'
// import Detail from './Detail'

import style from './index.less'
// import style from './songlists.less'
const { Search } = Input
const {
  sex,
  status,
  type,
  racearea,
} = config
interface Iprops {
  signupdetail: any,
  loading: any,
  dispatch: any,
  location: any,
}
interface Istate {
  previewVisible: boolean,
  previewImage: string,
  modalVisible: boolean,
  activeDetail: any,
}

class Index extends React.PureComponent<Iprops, Istate> {
  public state = {
    previewVisible: false,
    previewImage: '',
    modalVisible: false,
    activeDetail: {},
  }
  public showModal = () => {
    this.props.dispatch({
      type: 'signupdetail/updateState',
      payload: {
        modalVisible: true,
      },
    })
  }
  public updataDetail = (item: any) => {
    this.setState({
      modalVisible: true,
      activeDetail: item,
    })
  }
  public handleSearch = (val: string) => {
    this.props.dispatch({
      type: 'signupdetail/query',
      payload: {
        keyword: val,
      },
    })
    // this.props.dispatch({
    //   type: 'signupdetail/query',
    //   payload: {},
    // })
  }
  public handleExport = () => {
    this.props.dispatch({
      type: 'signupdetail/exportExcel',
      payload: {
        per_page: 500,
      },
    })
    // let option = {}
    // const { user } = this.props.signupdetail
    // const { list } = user
    // option.fileName = 'user-detail'
    // option.datas = [
    //   {
    //     sheetData: list,
    //     sheetName: 'sheet',
    //     // sheetFilter: ['two', 'one'],
    //     sheetHeader: ['id', '投票人', '电话', 'ip', '投票歌曲', '投票次数'],
    //   },
    // ]

    // let toExcel = new ExportJsonExcel(option)
    // toExcel.saveExcel()
  }
  public render () {
    const {
      previewVisible,
      previewImage,
      modalVisible,
      activeDetail,
    } = this.state
    const {
      signupdetail, loading, dispatch,
    } = this.props
    const {
      list, pagination, topSongList,
    } = signupdetail
    const handlePreview = (visible: boolean, url: string = '') => {
      this.setState({
        previewImage: url,
        previewVisible: visible,
      })
      // dispatch({
      //   type: 'signupdetail/updateState',
      //   payload: {
      //     previewImage: url,
      //     previewVisible: visible,
      //   },
      // })
    }
    const handleCancel = () => {
      this.setState({
        modalVisible: false,
      })
    }
    // const { query, pathname } = location
    // const sex = ['保密', '男', '女']
    // const status = ['未线下确认', '线下确认报名，未支付', '已支付']
    const columns = [
      {
        title: '序号',
        key: '1',
        render: (text: any, record: any, index: number) => <span>{index + 1}</span>,
      },
      {
        title: 'id',
        key: '2',
        dataIndex: 'id',
      },
      {
        title: '形式',
        key: '3',
        dataIndex: 'type',
        render: (text: string) => <span>{type[text]}</span>,
      },
      {
        title: '图片',
        key: '4',
        dataIndex: 'photo',
        render: (text: string) => <div onClick={() => handlePreview(true, text)}><img alt="图片" style={{ cursor: 'pointer' }} width={30} src={text} /></div>,
      },
      {
        title: '姓名',
        key: '5',
        dataIndex: 'name',
      },
      {
        title: '电话',
        key: '6',
        dataIndex: 'telephone',
      },
      {
        title: '性别',
        key: '7',
        dataIndex: 'sex',
        render: (text: number) => <span>{sex[text]}</span>,
      },
      {
        title: '生日',
        key: '8',
        dataIndex: 'birthday',
        // render: item => <span>{item}</span>,
      },
      {
        title: '赛区',
        key: '9',
        dataIndex: 'racearea',
        render: (item: number) => <span>{racearea[item]}</span>,
      },
      {
        title: '地区',
        key: '10',
        dataIndex: '',
        render: (item: any) => <span>{item.province} / {item.city}</span>,
      },
      {
        title: '报名点',
        key: '11',
        dataIndex: 'shop_name',
      },
      {
        title: '歌曲',
        key: '12',
        dataIndex: 'signup_has_many_song',
        render: (item: any) => {
          return item.map((song: any, index: number) => {
            return <p key={index}>{song.simp_name}</p>
          })
        },
      },
      {
        title: '状态',
        key: '13',
        dataIndex: 'status',
        render: (text: number) => <span>{status[text]}</span>,
      },
      {
        title: '操作',
        key: '14',
        render: (item: any) => <Button onClick={() => this.updataDetail(item)}>修改</Button>,
      },
    ]
    const listProps = {
      pagination,
      dataSource: list,
      columns,
      loading: loading.effects['signupdetail/query'],
      onChange (page: any) {
        dispatch({
          type: 'signupdetail/query',
          payload: {
            current_page: page.current,
            per_page: page.pageSize,
          },
        })
        // dispatch(routerRedux.push({
        //   pathname,
        //   search: queryString.stringify({
        //     ...query,
        //     current_page: page.current,
        //     per_page: page.pageSize,
        //   }),
        // }))
      },
    }
    const modalProps = {
      item: activeDetail,
      visible: modalVisible,
      maskClosable: false,
      // confirmLoading: loading.effects['user/update'],
      title: '修改',
      width: '60%',
      topSongList,
      wrapClassName: 'vertical-center-modal',
      onOk (data: any) {
        dispatch({
          type: 'signupdetail/queryUpata',
          payload: {
            // modalVisible: false,
            ...data,
          },
        })
        handleCancel()
        // dispatch({
        //   type: 'signupdetail/updateState',
        //   payload: {
        //     modalVisible: false,
        //   },
        // })
      },
      onCancel () {
        handleCancel()
        // dispatch({
        //   type: 'signupdetail/updateState',
        //   payload: {
        //     modalVisible: false,
        //   },
        // })
      },
    }
    return (
      <Fragment>
        <div>
          <Row className="margin-bottom" type="flex" align="middle">
            <Col span={6}>
              <h3 style={{ marginBottom: 0 }}>报名详情</h3>
            </Col>
            <Col span={18} className={style.textRight}>
              <Button onClick={this.handleExport} style={{ marginRight: 10 }} loading={loading.effects['signupdetail/exportExcel']} type="primary">导出</Button>
              <Search style={{ width: 300 }} placeholder="请输入要搜索的姓名或电话" onSearch={this.handleSearch} enterButton={true} />
            </Col>
          </Row>
          <Table
            {...listProps}
            bordered={true}
            // scroll={{ x: 1200 }}
            // columns={columns}
            // simple
            rowKey="id"
          />
        </div>
        {modalVisible && <Model {...modalProps} />}
        <Modal visible={previewVisible} footer={null} onCancel={() => handlePreview(false)}>
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </Fragment>
    )
  }
}
export default connect(({
  signupdetail, loading, dispatch,
}: Iprops) => ({
  signupdetail, loading, dispatch,
}))(Index)



