import React from 'react'
// import PropTypes from 'prop-types'
import { Form, Input, Modal, DatePicker, Select, Cascader, Upload, Button, message } from 'antd'
import moment from 'moment'
import style from './index.less'
import SongModel from './SongModel'
// import config from '../signupConfig'
import { region } from '../region'
import { config } from '../signupConfig'

// const moment = require("moment").default
const FormItem = Form.Item
// const RadioGroup = Radio.Group
const { Option } = Select
// const { RangePicker } = DatePicker
const {
  racearea,
} = config

const formItemLayout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 14,
  },
}
const songId = {}
declare type UploadFileStatus = 'error' | 'success' | 'done' | 'uploading' | 'removed';
interface IuploadFile {
  uid: number;
  size: number;
  name: string;
  filename?: string;
  lastModified?: number;
  lastModifiedDate?: Date;
  url?: string;
  status?: UploadFileStatus;
  percent?: number;
  thumbUrl?: string;
  isNotImage?: boolean;
  originFileObj?: File;
  response?: any;
  error?: any;
  linkProps?: any;
  type: string;
}
interface Iprops {
  item: any,
  onOk: any,
  form: any,
  [propsname: string]: any
}
interface Istate {
  fileList: IuploadFile[],
  previewVisible: boolean,
  previewImage: string,
  songVisible: boolean,
  province: string,
  city: string,
}
class Model extends React.PureComponent<Iprops, Istate> {
  public state = {
    fileList: this.props.item.photo ? [{
      uid: -1,
      size: 1,
      type: 'select',
      name: '',
      url: this.props.item.photo,
    }] : [],
    previewVisible: false,
    previewImage: '',
    songVisible: false,
    province: this.props.item.province,
    city: this.props.item.city,
  }
  public handleOk = () => {
    const {
      item,
      onOk,
      form: {
        validateFields,
        getFieldsValue,
      },
    } = this.props
    const {
      province,
      city,
    } = this.state
    validateFields((errors: any) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
      }
      const sendData: any = {
      }
      if (typeof data.photo === 'string') {
        sendData.photo = data.photo
      } else {
        sendData.photo = data.photo.fileList[0] ? data.photo.fileList[0].response.data.url : ''
      }
      if (!sendData.photo) {
        message.error('请上传图片')
        return
      }
      sendData.birthday = moment(data.birthday).format('YYYY-MM-DD')
      sendData.province = province
      sendData.city = city
      const proId = data.area[0]
      const cityId = data.area[1]
      sendData.pro_id = proId
      sendData.city_id = cityId
      sendData.name = data.name
      sendData.racearea = data.racearea
      sendData.sex = data.sex
      sendData.type = data.type
      sendData.telephone = data.telephone
      sendData.data_song = JSON.stringify({
        0: data.song1,
        1: data.song2,
        2: data.song3,
      })
      sendData.id = item.id
      // sendData.jump_link = data.jump_link
      // sendData.sort = data.sort
      console.log(sendData, 22222)
      // data.address = data.address.join(' ')
      onOk(sendData)
    })
  }
  public handleCancel = () => {
    this.setState({
      previewVisible: false,
    })
  }
  public handlePreview = (file: any) => {
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    })
  }
  public handleChange = ({ fileList }: any) => {
    this.setState({
      fileList,
    })
  }
  public range = (start: number, end: number) => {
    const result = []
    for (let i = start; i < end; i++) {
      result.push(i)
    }
    return result
  }
  public disabledDate = (current: any) => {
    // Can not select days before today and today
    return current && current < moment().endOf('day')
  }
  public disabledDateTime = () => {
    return {
      disabledHours: () => this.range(0, 24).splice(4, 20),
      disabledMinutes: () => this.range(30, 60),
      disabledSeconds: () => [55, 56],
    }
  }
  public handleSelect = (val: any, option: any) => { // 临时保存当前选中的地区
    this.state.province = option[0].value
    this.state.city = option[1].value
  }
  public checkInSonglist = (id: string) => {
    const { topSongList } = this.props
    if (songId[id]) {
      return true
    }
    let temp = false
    // for (let i = 0; i < topSongList.length; i++) {
    //   // console.log(topSongList[i], 'topSongList[i]')
    //   if (topSongList[i].s_id === Number(id)) {
    //     songId[id] = true
    //     temp = true
    //     break
    //   }
    // }
    for (const value of topSongList) {
      if (value.s_id === Number(id)) {
        songId[id] = true
        temp = true
        break
      }
    }
    return temp
  }
  public handleCheck = (rule: any, value: any, callback: any) => { // 验证歌曲id是否在100首歌中
    if (this.checkInSonglist(value)) {
      callback()
    } else {
      callback('输入的歌曲id不在100首歌里面')
    }
  }
  public render () {
    const {
      item = {},
      topSongList = [],
      onOk,
      form: {
        getFieldDecorator,
      },
      ...modalProps
    } = this.props
    const {
      fileList, previewVisible, previewImage, songVisible,
    } = this.state
    const modalOpts = {
      ...modalProps,
      onOk: this.handleOk,
    }
    const uploadButton = (
      <div>
        <div className="ant-upload-text">上传图片</div>
      </div>
    )
    const songModelCancel = (visible: boolean) => {
      this.setState({
        songVisible: visible,
      })
    }
    const SongmodalProps = {
      item: topSongList,
      visible: songVisible,
      maskClosable: false,
      title: '修改',
      width: '60%',
      topSongList,
      wrapClassName: 'vertical-center-modal',
      onOk () {
        songModelCancel(false)
      },
      onCancel () {
        songModelCancel(false)
      },
    }
    const dateFormat = 'YYYY-MM-DD'
    // const isDate = `${moment(item.birthday, dateFormat)}` === 'Invalid Date'
    // console.log(moment(item.birthday, dateFormat))
    return (
      <div>
        <Modal {...modalOpts}>
          <Form layout="horizontal" className={style.form}>
            <FormItem label="姓名" hasFeedback={true} {...formItemLayout}>
              {getFieldDecorator('name', {
                initialValue: item.name,
                rules: [
                  {
                    required: true,
                    message: '请输入姓名',
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="参赛形式"
              hasFeedback={true}
            >
              {getFieldDecorator('type', {
                initialValue: `${item.type}`,
                rules: [
                  { required: true, message: '请选择参赛形式' },
                ],
              })(<Select placeholder="请选择参赛形式">
                <Option value="0">个人</Option>
                <Option value="1">团队</Option>
              </Select>)}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="性别"
              hasFeedback={true}
            >
              {getFieldDecorator('sex', {
                initialValue: `${item.sex}`,
                rules: [
                  { required: true, message: '请选择参赛形式' },
                ],
              })(<Select placeholder="请选择性别">
                <Option value="1">男</Option>
                <Option value="2">女</Option>
              </Select>)}
            </FormItem>
            <FormItem label="电话" hasFeedback={true} {...formItemLayout}>
              {getFieldDecorator('telephone', {
                initialValue: item.telephone,
                rules: [
                  {
                    required: true,
                    message: '请输入电话',
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="地区"
              hasFeedback={true}
            >
              {getFieldDecorator('area', {
                initialValue: [item.pro_id, item.city_id],
                rules: [{ type: 'array', required: true, message: '请选择地区!' }],
              })(<Cascader onChange={this.handleSelect} filedNames={{ label: 'value', value: 'id' }} options={region} />)}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="赛区"
              hasFeedback={true}
            >
              {getFieldDecorator('racearea', {
                initialValue: item.racearea,
                rules: [
                  { required: true, message: '请选择赛区' },
                ],
              })(<Select placeholder="请选择赛区">
                {
                  racearea.map((d, index) => {
                    return d !== null ? <Option key={index} value={index}>{d}</Option> : ''
                  })
                }
              </Select>)}
            </FormItem>
            <FormItem label="生日" {...formItemLayout} hasFeedback={true}>
              {getFieldDecorator('birthday', {
                initialValue: moment(item.birthday, dateFormat),
                // initialValue: item.birthday,
                rules: [
                  {
                    required: true,
                    message: '请选择日期',
                  },
                ],
              })(<DatePicker />)}
            </FormItem>
            <FormItem label="报名点" hasFeedback={true} {...formItemLayout}>
              堂会KTV（区庄店）
            </FormItem>
            <FormItem label="歌曲" hasFeedback={true} {...formItemLayout}>
              {getFieldDecorator('song1', {
                initialValue: item.signup_has_many_song[0] ? item.signup_has_many_song[0].song_id : '',
                rules: [
                  {
                    required: true,
                    message: '请输入歌曲',
                  },
                  {
                    validator: this.handleCheck,
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem label=" " hasFeedback={true} {...formItemLayout} className={style.noLabel}>
              {getFieldDecorator('song2', {
                initialValue: item.signup_has_many_song[1] ? item.signup_has_many_song[1].song_id : '',
                rules: [
                  {
                    required: true,
                    message: '请输入歌曲',
                  },
                  {
                    validator: this.handleCheck,
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem label=" " hasFeedback={true} {...formItemLayout} className={style.noLabel}>
              {getFieldDecorator('song3', {
                initialValue: item.signup_has_many_song[2] ? item.signup_has_many_song[2].song_id : '',
                rules: [
                  {
                    required: true,
                    message: '请输入歌曲',
                  },
                  {
                    validator: this.handleCheck,
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem label=" " hasFeedback={true} {...formItemLayout} className={style.noLabel}>
              <Button onClick={() => songModelCancel(true)}>查看歌曲列表</Button>
            </FormItem>
            <FormItem {...formItemLayout} className={style.upload} >
              {getFieldDecorator('photo', {
                initialValue: item.photo,
                rules: [
                  {
                    required: true,
                    message: '请上传图片',
                  },
                ],
            })(<Upload
              name="file"
              action="/v4.0/base/material"
              listType="picture-card"
              fileList={fileList}
              data={{ folder: 'singing' }}
              // showUploadList={false}
              onPreview={this.handlePreview}
              onChange={this.handleChange}
              className="upload"
            >
              {fileList.length >= 1 ? null : uploadButton}
            </Upload>)}
            </FormItem>
          </Form>
        </Modal>
        {songVisible && <SongModel {...SongmodalProps} />}
        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </div>
    )
  }
}
export default Form.create()(Model)



