interface Iprops {
  topSongList: any[]
}
interface Istate {
  list: any[]
}
class Model extends React.PureComponent<Iprops, Istate> {
  public state = {
    list: this.props.topSongList,
  }
  public handleSearch = (val: string) => {
    const {
      topSongList,
    } = this.props
    const newList = topSongList.filter((item) => {
      return item.simp_name.indexOf(val) !== -1
    })
    // console.log(newList)
    this.setState({
      list: newList,
    })
  }
  public render () {
    const {
      ...modalProps
    } = this.props
    const {
      list,
    } = this.state
    // console.log(list, '222')
    const columns = [
      {
        title: 'id',
        dataIndex: 'id',
      },
      {
        title: '歌曲id',
        dataIndex: 's_id',
      },
      {
        title: '歌名',
        dataIndex: 'simp_name',
      },
      {
        title: '歌手',
        dataIndex: 'singer_name',
      },
    ]
    const listProps = {
      // pagination,
      dataSource: list,
      columns,
      // loading: loading.effects['areadetail/query'],
      // onChange (page) {
      //   // dispatch(routerRedux.push({
      //   //   pathname,
      //   //   search: queryString.stringify({
      //   //     ...query,
      //   //     current_page: page.current,
      //   //     per_page: page.pageSize,
      //   //   }),
      //   // }))
      // },
    }
    return (
      <div>
        <Modal {...modalProps}>
          <Search style={{ width: 300, marginBottom: 10 }} placeholder="请输入要搜索的歌曲名称" onSearch={this.handleSearch} enterButton={true} />
          <Table
            {...listProps}
            bordered={true}
            // scroll={{ x: 1200 }}
            // columns={columns}
            // simple
            rowKey={record => record.id}
          />
        </Modal>
        {/* <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal> */}
      </div>
    )
  }
}
// modal.propTypes = {
//   topSongList: PropTypes.array,
// }

export default Model



